import { query, mutate } from '../utils/graphql';
import * as loadingActions from './loadingActions';
import { idTimeToString } from '../utils/converter';

export const AREA_LIST_KNOWN = 'AREA_LIST_KNOWN';
export const STATION_LIST_KNOWN = 'STATION_LIST_KNOWN';
export const STATION_WEATHER_LIST_KNOWN = 'STATION_WEATHER_LIST_KNOWN';
export const STATION_HEIGHT_LIST_KNOWN = 'STATION_HEIGHT_LIST_KNOWN';
export const STATION_LIST_APPENDED = 'STATION_LIST_APPENDED';
export const STATION_IS_ADDING_UPDATE = 'STATION_IS_ADDING_UPDATE';
STATION_WEATHER_LIST_KNOWN
const GET_STATION_LIST_BY_USER = `
  query GET_STATION_LIST_BY_USER($token: String!) {
    getAreaList(token: $token) {
      areaList {
        id
        name
        description
      }
    }
    getStationListByUser(token: $token) {
      stationList {
        id
        name
        latitude
        longitude
      }
    }
  }
`;

const GET_DEVICE_DATA_LIST_BY_STATION = `
  query GET_DEVICE_DATA_LIST_BY_STATION($token: String!, $stationId: String!) {
    getDeviceDataListByStationId(token: $token, stationId: $stationId) {
      deviceDataList {
        id
        deviceId
        stationId
        type
        value
      }
    }
    getWeatherListByStationId(token: $token, stationId: $stationId) {
      weatherList {
        id
        areaId
        precipitation
        temperature
      }
    }
  }
`;

const ADD_STATION = `
  mutation ADD_STATION(
    $token: String!,
    $name: String!,
    $areaName: String!,
    $lat: String!,
    $lng: String!
  ){
    addStation(
      token: $token,
      station: {
        name: $name,
        areaName: $areaName,
        latitude: $lat,
        longitude: $lng,
      }
    ){
      errorMsg
      id
      name
      areaId
      latitude
      longitude
      description
    }
  }
`;

export const areaListKnown = (areaList: any) => ({
  type: AREA_LIST_KNOWN,
  areaList,
});

export const stationListKnown = (stationList: any) => ({
  type: STATION_LIST_KNOWN,
  stationList,
});

export const stationListAppended = (station: any) => ({
  type: STATION_LIST_APPENDED,
  station,
});

export const stationHeightListKnown = (dataList: any) => ({
  type: STATION_HEIGHT_LIST_KNOWN,
  dataList,
});

export const stationWeatherListKnown = (weatherList: any) => ({
  type: STATION_WEATHER_LIST_KNOWN,
  weatherList,
});

export const isAddingStation = (isAdding: any, lat: string = null, lng: string = null) => ({
  type: STATION_IS_ADDING_UPDATE,
  isAdding,
  lat,
  lng,
});


export const getUserStations: Function = () => async (
  dispatch: Function,
  getState: Function
) => {
  dispatch(loadingActions.loadingStart());

  const token = localStorage.getItem('sewage:token');
  const data: any = await query(GET_STATION_LIST_BY_USER, { token });
  dispatch(areaListKnown(data.getAreaList.areaList));
  dispatch(stationListKnown(data.getStationListByUser.stationList));
  dispatch(loadingActions.loadingEnd());
};

export const getStationData: Function = (stationIdTime: string) => async (
  dispatch: Function,
  getState: Function
) => {
  dispatch(loadingActions.loadingStart());
  const stationId = idTimeToString(stationIdTime);

  const token = localStorage.getItem('sewage:token');
  const data: any = await query(GET_DEVICE_DATA_LIST_BY_STATION, { token, stationId });
  dispatch(stationHeightListKnown(data.getDeviceDataListByStationId.deviceDataList))
  dispatch(stationWeatherListKnown(data.getWeatherListByStationId.weatherList))
  dispatch(loadingActions.loadingEnd());
};


export const addStation: Function = (station: {}) => async (
  dispatch: Function,
  getState: Function
) => {
  dispatch(loadingActions.loadingStart());

  const token = localStorage.getItem('sewage:token');
  const data: any = await mutate(ADD_STATION, { token, ...station });

  dispatch(isAddingStation(false))
  dispatch(stationListAppended(data.addStation));
  dispatch(loadingActions.loadingEnd());
};
